'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';

interface Episode {
  id: string;
  title: string;
  isLocked: boolean;
}

export default function EpisodeList() {
  const router = useRouter();
  const [episodes] = useState<Episode[]>([
    { id: '1', title: '第1集', isLocked: false },
    { id: '2', title: '第2集', isLocked: false },
    { id: '3', title: '第3集', isLocked: true },
    { id: '4', title: '第4集', isLocked: true },
    { id: '5', title: '第5集', isLocked: true },
  ]);

  const handleEpisodeClick = (episode: Episode) => {
    if (episode.isLocked) {
      router.push(`/payment?episodeId=${episode.id}`);
    } else {
      // TODO: 切换到对应剧集
      console.log('切换到剧集:', episode.id);
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto mt-8">
      <h2 className="text-xl font-bold mb-4">选集列表</h2>
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
        {episodes.map((episode) => (
          <button
            key={episode.id}
            onClick={() => handleEpisodeClick(episode)}
            className={`p-4 rounded-lg text-center transition-colors ${
              episode.isLocked
                ? 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                : 'bg-white/10 text-white hover:bg-white/20'
            }`}
          >
            <div className="relative">
              {episode.title}
              {episode.isLocked && (
                <span className="absolute -top-2 -right-2 text-xs bg-yellow-500 text-black px-1 rounded">
                  锁定
                </span>
              )}
            </div>
          </button>
        ))}
      </div>
    </div>
  );
} 