'use client';

import Image from 'next/image';
import { useState } from 'react';

export default function UserAvatar() {
  const [isLoggedIn] = useState(false);

  return (
    <div className="absolute top-4 right-4 z-10">
      {isLoggedIn ? (
        <div className="relative w-10 h-10 rounded-full overflow-hidden border-2 border-white shadow-lg">
          <Image
            src="/avatar.png"
            alt="用户头像"
            fill
            className="object-cover"
          />
        </div>
      ) : (
        <button className="px-4 py-2 bg-white/10 backdrop-blur-sm rounded-full text-white hover:bg-white/20 transition-colors">
          登录
        </button>
      )}
    </div>
  );
} 